import React, { useRef, useEffect } from 'react'

export default ({ width, height, devicePixelRatio }) => {
  const ref = useRef()

  useEffect(() => {
    if (ref.current !== undefined) {
      const middlePoint = [
        Math.floor(width / 2), 
        Math.floor(height / 2)
      ]
      
      const context2d = ref.current.getContext('2d')
      context2d.beginPath()
      context2d.moveTo(...middlePoint)
      context2d.lineTo([middlePoint + 10, middlePoint])
      context2d.lineTo([middlePoint + 10, middlePoint + 10])
      context2d.lineTo([middlePoint, middlePoint + 10])
      context2d.closePath()
      context2d.stroke()

      console.log(
        'middle line', 
        Math.floor(width / 2), 
        Math.floor(height / 2)
      )
    }
  })

  return <canvas 
        ref={(domElement) => ref.current = domElement}
        width={width * devicePixelRatio} 
        height={height * devicePixelRatio} 
        style={{width, height, display: 'block'}} 
    />
}